import React from 'react';
import Spiner from '../spiner/spiner';
import Error from '../error/error';
import '../items-detalus/items-detalus.css';

const Detal = ({ items, label, value }) => {
    return (
        <p className="card-text-dec">{label} {items[value]}</p>
    )
}

export {
    Detal
}

const ItemsDetal = (props) => {

    const { items, loading, error } = props;
 
    return (<div className="mon">

        { error ? <Error /> : loading ? <Spiner /> :
            (<div className="card text-white bg-primary mb-3">
                <div className="card-header">Подробная информация</div>
                <div className="card-body">
                    <img src={items.img}></img>
                    <div>
                        {
                            React.Children.map(props.children, (child) => {
                                return React.cloneElement(child, { items });
                            })
                        }
                    </div>
                </div>
            </div>)
        }
    </div>)
}

export default ItemsDetal;
