import React from 'react';
import './item.css';
import Spiner from '../spiner/spiner';
import Error from '../error/error';
import { withRouter } from 'react-router-dom';


const Item = (props) => {

    const Items = (items) => {

        const { selectItems, history, Api } = props;
        items.splice(6)
        return items.map((item) => {

            const { id } = item;
            const disabled = selectItems == id ? 'list-group-item list-group-item-action active' : 'list-group-item list-group-item-action';
            return (<a key={id} onClick={() => { Api(id); history.push(id); }}
                className={disabled} >
                {item.name}
            </a>)
        })
    }


    const { items, loading } = props;
    return (
        <div className="people" >
            { loading ? <Spiner /> : (<div className="list-group">
                <Error>
                    {Items(items)}
                </Error>
            </div>)}
        </div>)
}



export default withRouter(Item);