import React from 'react';

const HocDetalis = (View, getData) => {

    return class extends React.Component {

        state = {
            items: {},
            loading: true,
            error: false
        }

        componentDidMount() {

            const { selectItems } = this.props;
            getData(selectItems).then((items) => {
                this.setState({ items, loading: false });
            }).catch(this.my_catch);
        }

        componentDidUpdate(oldProps) {

            if (this.props.selectItems === oldProps.selectItems) {
                return
            }
            this.setState({ loading: true });
            this.updata()
        }

        updata = () => {

            getData(this.props.selectItems).then((items) => { this.setState({ items, loading: false }); }).catch(this.my_catch);
        }

        render() {

            return <View {...this.props} {...this.state} />

        }
    }
}

export default HocDetalis;