import React, { useState } from 'react';
import { Detal } from '../items-detalus/items-detalus';
import { withRouter } from 'react-router-dom';

const AppCreacte = (props) => {

    const Hhh = props.create;
    const Hht = props.detalis;

    const { id } = props.match.params;
    const [selectItems, Api] = useState(id ? id : 2);

    return (<div className="Del">
        <Hhh Api={Api} selectItems={selectItems} />
        <Hht selectItems={selectItems}>
            <Detal label="Имя:" value="name" />
            <Detal label="Год:" value="created" />
        </Hht>
    </div>)
}

export default withRouter(AppCreacte)
