import { render } from '@testing-library/react';
import React from 'react';
import { Redirect, Link, withRouter } from 'react-router-dom';
import './secret-content.css';

const Secret = ({ isLogin, history }) => {

    if (isLogin) {
        return (<div className="secret-content">
            <p>Ваш секретный контент</p>
            <button>Скачать</button>
        </div>)
    }
    return (<div className="secret-content">
        <p>Чтобы скачать, нужно войти!!!</p>
        <button onClick={() => history.push("/login")}>Войти сейчас!</button>
    </div>)
}

export default withRouter(Secret);