import React from 'react';
import './heder.css';
import { Link } from 'react-router-dom';

const Heder = () => {
    return (
        <div className="header">
            <ul>
                <li><Link className="header-name" to="/">Star My DB</Link></li>
                <li><Link to="/people/">Люди</Link></li>
                <li><Link to="/planet/">Планеты</Link></li>
                <li><Link to="/starship/">Карабли</Link></li>
                <li><Link to="/login">Войти</Link></li>
                <li><Link to="/secret">Секрет</Link></li>
            </ul>
        </div>
    )
}

export default Heder;