export default class Api {

    _url = 'https://swapi.dev/api';

    get = async (url) => {
        const res = await fetch(`${this._url}${url}`);
        if (!res.ok) {
            throw new Error('Ошибка сети', res.status);
        }
        return await res.json();
    }

    getStarshipsAll = async () => {
        const planets = await this.get('/starships/');
        return planets.results.map(this._transFormShip)
    }

    getStarshipsId = async (id) => {
        const planet = await this.get(`/starships/${id}/`);
        return this._transFormShip(planet);
    }

    getPlanetsAll = async () => {
        const planets = await this.get('/planets/');
        return planets.results.map(this._transFormPlanet)
    }

    getPlanetsId = async (id) => {
        const planet = await this.get(`/planets/${id}/`);
        return this._transFormPlanet(planet);
    }

    getPeopleAll = async () => {
        const people = await this.get('/people/');
        return people.results.map(this._transFormPeople)
    }

    getPeopleId = async (id) => {
        const people = await this.get(`/people/${id}/`);
        return this._transFormPeople(people);
    }

    _transFormShip(pl) {

        const id = (pl.url.split('/')[5]);
        return {
            id: id,
            name: pl.name,
            created: pl.created,
            length: pl.length, 
            img: `https://starwars-visualguide.com/assets/img/starships/${id}.jpg`,
        }
    }

    _transFormPeople(pl) {

        const id = (pl.url.split('/')[5]);
        return {
            id: id,
            name: pl.name,
            gender: pl.gender,
            birth_year: pl.birth_year,
            hair_color: pl.hair_color,
            img: `https://starwars-visualguide.com/assets/img/characters/${id}.jpg`,
        }
    }

    _transFormPlanet(pl) {

        const id = (pl.url.split('/')[5]);
        return {
            id: id,
            name: pl.name,
            img: `https://starwars-visualguide.com/assets/img/planets/${id}.jpg`,
            created: pl.created,
            diameter: pl.diameter
        }
    }

}


