import React from 'react';
import '../error/error.css';
import Er from './star_PNG41482.png';

export default class Error extends React.Component {

    state = {
        err: false
    }

    componentDidCatch() {
        this.setState({ err: true });
    }

    render() {
        if (!this.state.err) {
            return this.props.children;
        }
        return (<div className="textError" >
            <img src={Er}></img>
            <h2 className="textErrorH">Ошибка при загрузке планеты</h2>
            <p>Попробуйте выполнить запрос позже</p>
        </div>);
    }
}
