import React from 'react';
import '../info/info.css';
import Spiner from '../spiner/spiner';
import Error from '../error/error';


export default class Info extends React.Component {

    state = {
        planet: {},
        loading: true
    }

    componentDidMount() {
        this.undete();
        this.interval = setInterval(this.undete, 55000)
    }

    componentWillUnmount() {
        setInterval(this.interval)
    }

    st = (planet) => {
        this.setState({ planet, loading: false });
    }

    my_catch = () => {
        this.setState({
            loading: false
        })
    }

    undete = () => {
        const m = Math.floor((Math.random(1) * 15));
        this.props.getPlanetsId(m).then((this.st)).catch(this.my_catch);
    }

    render() {

        const { planet: { img, name, created, diameter }, loading } = this.state;
        return (
            <div className="info" >
                <Error>
                    {
                        (loading) ? <Spiner /> : (<div className="info-conainer">
                            <div className="info-grid-img">
                                <img className="info-grid-image" src={img}></img>
                            </div>
                            <div className="info-grid-text">
                                <p>Имя планеты : {name}</p>
                                <p>Дата существования: {created}</p>
                                <p>Диаметр: {diameter}</p>
                            </div>
                        </div>)
                    }
                </Error>
            </div >)
    }
}
