import React, { useState } from 'react';
import './App.css';
import Info from './component/info/info';
import Header from './component/header/header';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom'
import { CreatePeople, CreatePlanet, CreateStarhip } from './component/sw-component/sw-items';
import { PeopleDetalis, PlanetDetalis, StarhipDetalis } from './component/sw-component/sw-delais';
import Api from './api/api';
import AppCreacte from './component/hoc-item/app-create';
import Login from './login/loging';
import Secret from './login/login-content';
import NotFound from './component/not-found/not-found';

const App = (props) => {

  const [isLogin, btnlogin] = useState(false)

  console.log(props);
  return (
    <div className="App" >
      <BrowserRouter>
        <Header />
        <Info getPlanetsId={new Api().getPlanetsId} />
        <Switch>

          <Route path="/" exact render={() => <div>Привет, рад привестовать Вас на моем сайте!</div>} />
          <Route path="/people/:id?" render={() => <AppCreacte create={CreatePeople} detalis={PeopleDetalis} />} />
          <Route path="/planet/:id?" render={() => <AppCreacte create={CreatePlanet} detalis={PlanetDetalis} />} />
          <Route path="/starship/:id?" render={() => <AppCreacte create={CreateStarhip} detalis={StarhipDetalis} />} />

          <Route path="/login" render={() => <Login isLogin={isLogin} btnlogin={btnlogin} />} />
          <Route path="/secret" render={() => <Secret isLogin={isLogin} />} />

          <Route component={NotFound} />

        </Switch>
      </BrowserRouter>

      <footer>
        Подвал ваш!
        </footer>
    </div >
  );

}


export default App;